html, body, #root {
    margin: 0;
    padding: 0;
    background-color: #6570a6;
    height: 100%;
}

@keyframes fadeInBlack {
    0% {
        background-color: rgba(0, 0, 0, 0);
    }
    100% {
        background-color: rgba(0, 0, 0, 1);
    }
}

@keyframes zoomSpin {
    0% {
        transform: scale(1) rotate(0deg);
    }
    40% {
        transform: scale(3) rotate(180deg);
    }
    100% {
        transform: scale(20) rotate(720deg);
    }
}
